<template>
    <div class="position-absolute popper" ref="popper" v-show="showPopover">
        <div class="card shadow border border-0">
            <div class="card-header p-0 overflow-hidden">
                <div class="color-type"></div>
                <div class="container-fluid">
                    <div class="header-bg row align-items-center">
                        <div class="col-12 p-0">
                            <p><b>{{ titleInfo }}</b><sup class="ms-1"> {{ titleInfoSmall }}</sup></p>
                        </div>
                    </div>
                </div>

            </div>
            <div class=" card-body">
                <div class="container-fluid px-0">

                    <div v-for="(value, name, index) in  HSHoverInfo " :key="index" class="row align-items-center p-1">
                        <div class="col-6">
                            <span>{{ name }}</span>
                        </div>
                        <div class="col-6">
                            <p v-html="`${currentRegion[value.key]} ${value.suffixTxt}`"></p>
                        </div>
                    </div>
                </div>


            </div>

            <div class="card-footer bg-transparent">
                <span>{{ currentRegion.statut }}</span>
                <status-component :status="currentRegion.statut"></status-component>
            </div>
        </div>
    </div>
</template>


<script setup>


import { ref, watch, onMounted, onUnmounted } from "vue";
import StatusComponent from "@/components/app-ui/StatusComponent";
import { createPopper } from '@popperjs/core';
import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();



const props = defineProps({

    showPopover: {
        type: Boolean,
        default: false
    },
    positions: {
        type: Object,

    }
});


const { currentColor, currentRegion, HSHoverInfo, titleInfo, titleInfoSmall } = storeToRefs(regionsStore);

let pInstance = null;
let virtualElement = null;
const popper = ref(null);


function initPopover() {
    virtualElement = {
        getBoundingClientRect: generateGetBoundingClientRect(),
    };
    pInstance = createPopper(virtualElement, popper.value, {
        placement: 'top',
        modifiers: [
            {
                name: 'offset',
                options: {
                    offset: ({ placement }) => {
                        if (placement === 'bottom') {
                            return [0, 30];
                        }

                        if (placement === 'top') {
                            return [0, 30];
                        }

                        return [];
                    },
                }
            },
        ]
    });
}

function updatePopover(x, y) {
    virtualElement.getBoundingClientRect = generateGetBoundingClientRect(x, y);
    pInstance.update();
}

function generateGetBoundingClientRect(x = 0, y = 0) {
    return () => ({
        width: 0,
        height: 0,
        top: y,
        right: x,
        bottom: y,
        left: x,
    });
}

watch(props.positions, () => {
    updatePopover(props.positions.x, props.positions.y);
});

onMounted(() => {
    initPopover();
});

onUnmounted(() => {
    pInstance.destroy();
});

</script>




