<template>
    <orbital-component @OnHSClick="HSClick" />
</template>


<script setup>

import OrbitalComponent from "@/components/orbital/OrbitalComponent";
import { useRouter } from "vue-router";
import { useAppDataStore } from "@/stores/appData";

const appDataStore = useAppDataStore();
appDataStore.setCurrentOrbital("listOrbitals");

const router = useRouter();

function HSClick(id) {
    const path = `orbitals/${id}`;
    router.push(path);
}

</script>
