<template>
    <div class="parent-video d-flex align-items-center">
        <div class="ratio ratio-16x9 h-100">
            <video controls>
                <source :src="_src" type="video/mp4">
                <p>Votre navigateur ne prend pas en charge les vidos HTML5.
                    Voici <a :href="videoPath">un lien pour tlcharger la vido</a>.</p>
            </video>
        </div>
    </div>
</template>


<script setup>

import { ref, watch } from "vue";
import axios from "axios";

const videoPath = ref("@/assets/videos/video-sample.mp4");
const _src = ref("");

watch(videoPath, () => {
    _src.value = _getSource();
}, { immediate: true });

async function _getSource() {
    try {
        const response = await axios.get(videoPath.value);
        return response.data;
    } catch (error) {
        console.error('Error loading video:', error);
    }

}

</script>

<style lang="scss" scoped>
.parent-video {
    height: 100vh;
    overflow: hidden;
}
</style>