<template>
    <div class="row scrollable-pages">

        <div class="col-12">
            <LineWithTitle>
                <svg xmlns="http://www.w3.org/2000/svg" width="1.6em" height="1.6em" fill="currentColor"
                    class="bi bi-file-earmark-lock2-fill me-1" viewBox="0 0 16 16">
                    <path d="M7 7a1 1 0 0 1 2 0v1H7V7z" />
                    <path
                        d="M9.293 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V4.707A1 1 0 0 0 13.707 4L10 .293A1 1 0 0 0 9.293 0zM9.5 3.5v-2l3 3h-2a1 1 0 0 1-1-1zM10 7v1.076c.54.166 1 .597 1 1.224v2.4c0 .816-.781 1.3-1.5 1.3h-3c-.719 0-1.5-.484-1.5-1.3V9.3c0-.627.46-1.058 1-1.224V7a2 2 0 1 1 4 0z" />
                </svg>
                Administration
            </LineWithTitle>
        </div>

        <div class="col-md-4 col-lg-3 m-0 p-0 px-3">
            <side-filter :is-loading="isLoading" @onOrbitalSwitched="orbitalSwitched" ref="sideFilterRef"></side-filter>
        </div>

        <div class="col-md-8 col-lg-9 m-0 p-0">
            <list-cards :fetch-by="6" :show-admin="true"></list-cards>
        </div>

        <to-top-btn>to top</to-top-btn>

    </div>
</template>


<script setup>



import SideFilter from "@/components/app-ui/SideFilter";
import ListCards from "@/components/orbital/search/ListCards";
import ToTopBtn from "@/components/app-ui/ToTopBtn";
import LineWithTitle from "@/components/app-ui/LineWithTitle";

import { ref, onBeforeMount } from "vue";
import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const { isLoading } = storeToRefs(regionsStore);

const appDataStore = useAppDataStore();
const { currentOrbital } = storeToRefs(appDataStore);

const sideFilterRef = ref(null);

function orbitalSwitched() {
    _fetchData();
}

onBeforeMount(() => {
    if (currentOrbital.value.isOrbitalSelection) {
        appDataStore.setCurrentOrbital('villa1');
    }
    _fetchData();
});

function _fetchData() {
    regionsStore.fetchAuthRegions().then(() => {
        if (sideFilterRef.value) regionsStore.filterRegions(sideFilterRef.value.filters);
    });
}


</script>
