<template>
    <div class="pages">
        <iframe :src="frameUrl" width="100%" height="100%"></iframe>
    </div>
</template>


<script setup>

import { computed } from "vue";

const baseUrl = process.env.VUE_APP_B_BASE_URL;
const panoUrl = process.env.VUE_APP_PANORAMA_URL;

const frameUrl = computed(() => {
    return `${baseUrl}${panoUrl}`;
});

</script>