<script setup>

import { ref } from 'vue'
import CarouselComponent from "../app-ui/CarouselComponent"
import { transitionEnter, transitionLeave } from "@/utils/hsDetailsTransition";
import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";
import { useCaptureStore } from "@/stores/capture";
import { storeToRefs } from 'pinia';


defineEmits([
    'onCloseDetails'
])

defineProps({
    canShow: {
        type: Boolean,
        default: true
    },
});


const regionsStore = useRegionsStore();
const appDataStore = useAppDataStore();
const captureStore = useCaptureStore();

const { currentColor, currentRegion, AllInfo, titleInfo, titleInfoSmall } = storeToRefs(regionsStore);
const { currentOrbital } = storeToRefs(appDataStore);


const screenshot = ref(null);

const items = ref([
    {
        url: "",
        title: "Plan 3D",
        desc: ""
    },
    {
        url: "",
        title: "Plan 2d",
        desc: ""
    }
]);


const setImageSrc = () => {
    const apptName = currentRegion.value.nom;
    items.value[0].url = `assets/images/lots/${currentOrbital.value.id}/3d/${apptName}.jpg`;
    items.value[1].url = `assets/images/lots/${currentOrbital.value.id}/2d/${apptName}.jpg`;
};

function onEnter(el) {
    transitionEnter(el);
}

function onLeave(el, done) {
    transitionLeave(el, done);
}

defineExpose({
    setImageSrc
});

</script>



