<template>
    <div class="FBO">
        <div class="filters-title mb-3">
            <span>Par Villa</span>
        </div>

        <div>
            <select v-model="selectedOrbital" class="custom-select form-select form-select-sm"
                aria-label=".form-select-sm select-orbital" id="filtreOrbital">
                <option v-for="(value, key, index) in listOrbitals" :key="index" :value="key">{{ value.title }}
                </option>
            </select>
        </div>
    </div>
</template>


<script setup>
import { ref, computed, watch } from "vue";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from 'pinia';

const emits = defineEmits([
    "onOrbitalSelect"
]);

const appDataStore = useAppDataStore();
const { availableOrbitals, currentOrbital } = storeToRefs(appDataStore);

const selectedOrbital = ref(currentOrbital.value.id);

const listOrbitals = computed(() => {
    const filteredEntries = Object.entries(availableOrbitals.value).filter((orb) => {
        return !orb[1].isOrbitalSelection
    });
    const filteredObject = Object.fromEntries(filteredEntries);
    return filteredObject;
});

watch(selectedOrbital, () => {
    emits('onOrbitalSelect', selectedOrbital.value);
});

</script>


<style lang="scss" scoped>
.custom-select {
    letter-spacing: .2em;
    font-size: .7em;
    text-transform: uppercase;
    padding: 0.5em 1em;
    border: 1px solid var(--app-primary-color-300);
}
</style>