import { Animator } from "./animator";
import { CanvasBuilder } from "./canvasBuilder";


class Orbital {
    constructor(container , orbitalData , regionsInfo){
        
        this.container = container;

        this._orbitalData = orbitalData;

        this._regionsInfo = regionsInfo;

        this.animationsPaths = [];

        this.animator, this.canvasBuilder;

    }

    init(configFile){
        this.hotspotsConfig = configFile;
        this._setup();
    }

    _setup() {
        
        const params = this._orbitalData.params;
        const levelsCount = params.animationsCount;
        let lPath = params.imagesFolder + params.preloadedFolder;
        
        for (let i = 0; i < levelsCount; i++) {
            
            const p = lPath.replace(/%row/g , i).replace(/%col/g , "?");
            this.animationsPaths.push(p);
        }

        this.canvasBuilder = new CanvasBuilder(this.container);

        const layers = this.canvasBuilder.createLayers();

        const orbitaConfig = {
            maxWidth : params.maxWidth,
            maxHeight : params.maxHeight,
            imagesFolder : params.imagesFolder,
            framesCount : params.framesCount,
            timeLineSpeed : params.timeLineSpeed,
            autoplay : params.autoplay,
            frameStart : params.frameStart,
            animStart : params.animStart,
            
            tilesFolder : this._orbitalData.tiles.tilesFolder,
            breakpoints_sizes : this._orbitalData.tiles.breakpoints_sizes,
            breakpoints : this._orbitalData.tiles.breakpoints,
            breakpoints_folder : this._orbitalData.tiles.breakpoints_folder,
            maxCache : this._orbitalData.tiles.maxTilesCache,
            cropSize : this._orbitalData.tiles.cropSize,

            zoomOptions : this._orbitalData.controls.zoom,
            dragOptions : this._orbitalData.controls.drag,
            HSColors : this._orbitalData.hotspots.color_HS
        };


        this.animator = new Animator(
            layers,
            this.animationsPaths ,
            orbitaConfig,
            this.hotspotsConfig,
            this._regionsInfo,
        );

        window.addEventListener("OnAnimatorReady" , () => this.appReady() , {once : true});
        this._setConfigStyles();
    }

    _setConfigStyles(){
        if(this.animator === undefined) return;
        document.getElementById(this.container).style.backgroundColor = this._orbitalData.params.bg_color;
    }

    appReady() {

        this.animationsPaths = null;

        this.animator.run();

        window.dispatchEvent(new CustomEvent("OnAppReady", {
            bubbles: false
        }));

        const useHS = this._orbitalData.hotspots.useHotspots;
        this.useHotspots(useHS);
    }
    

    getPercentLoaded() {
        if(this.animator === undefined) return 0;
        return this.animator.percentLoaded;
    }

    playOrbital(){
        this.animator.play();
    }

    stopOrbital(){
        this.animator.stop();
    }

    next(){
        this.animator.playFrame(false);
    }

    prev(){
        this.animator.playFrame(true);
    }

    setLevel(level){
        this.animator.switchAnimation(level);
    }
    
    filterHSByPrice(surface) {
        if(this.animator) this.animator.stop();
        if(this.animator.hotspots) this.animator.hotspots.setSurfaceFilter(surface);
    }
    filterHSByTypes(types) {
        if(this.animator) this.animator.stop();
        if(this.animator.hotspots) this.animator.hotspots.setTypesFilter(types);
    }

    setZoomOptions (opt){
        if(this.animator) this.animator._inputSystem.zoom.setOptions(opt);
    }

    setZoom(zoomIn) {
        if(this.animator) this.animator._inputSystem.zoom.setZoom(zoomIn);
    }

    getParam(param){
        return this._orbitalData.params[param];
    }
    getHSParam(param){
        return this._orbitalData.hotspots[param];
    }

    getCurrentFrame(){
        if(!this.animator) return 0;
        return this.animator._frameIndex;
    }

    useHotspots(useHS){
        if(!this.animator) return;
        if(this.animator.hotspots) this.animator.hotspots.useHotspots(useHS);
    }

    getEventsCanvas(){
        if(!this.animator) return null;
        return this.animator.eventsCanvas;
    }

    unmountOrbital() {
        if(this.animator === undefined) return;
        this.animator.cancel = true;
    }
    
}

export {Orbital}