<template>
    <div class="list-cards">
        <div class="container-fluid">
            <div class="row justify-content-left">
                <div class="col-sm-6 col-md-6 col-lg-4 mb-4" v-for="(card, _, index) in infinitData" :key="index">
                    <card-search :card-info="card" :types-colors="colorTypes" :is-admin="showAdmin"></card-search>
                </div>
            </div>
            <div class="no-search row text-center" v-if="Object.keys(infinitData).length === 0">
                <div class="col">
                    <p class="title py-5 m-0">Aucun resultat</p>
                    <div class="icon py-3">
                        <svg xmlns="http://www.w3.org/2000/svg" width="8em" height="8em" fill="currentColor"
                            class="bi bi-exclamation-square-fill" viewBox="0 0 16 16">
                            <path
                                d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6 4c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 4.995A.905.905 0 0 1 8 4zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z" />
                        </svg>
                    </div>
                </div>
            </div>
        </div>

        <!-- Infinite Trigger Element -->
        <span ref="infiniteElem" v-if="showTrigger"></span>
        <div v-if="showTrigger">
            <div class="d-flex justify-content-center">
                <div class="spinner-grow spinner" role="status">
                    <span class="visually-hidden">Loading...</span>
                </div>
            </div>
        </div>
        <!-- End Infinite Trigger Element -->

    </div>
</template>


<script setup>

import CardSearch from "@/components/orbital/search/CardSearch.vue";

import { useIntersectionObserver } from '@vueuse/core';

import { ref, watch } from "vue";

import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const appDataStore = useAppDataStore();

const props = defineProps({
    fetchBy: {
        type: Number,
        default: 6
    },
    showAdmin: {
        type: Boolean,
        default: false
    }
});

const { filteredRegions } = storeToRefs(regionsStore);
const { colorTypes } = storeToRefs(appDataStore);

const countPerFetch = ref(props.fetchBy);
const infiniteElem = ref(null);
const infinitData = ref({});
const showTrigger = ref(false);

useIntersectionObserver(
    infiniteElem,
    ([{ isIntersecting }]) => {
        const length = Object.keys(infinitData.value).length;
        if (isIntersecting) {
            const arr = Object.entries(infinitData.value);
            arr.push(...getMore());
            infinitData.value = Object.fromEntries(arr);
        }

        if (length >= Object.keys(filteredRegions.value).length || length <= 0) {
            showTrigger.value = false;
        }
    },
    {
        threshold: 0.1,
    }
);


function getMore() {
    const length = Object.entries(infinitData.value).length;
    return Object.entries(filteredRegions.value).slice(length, length + countPerFetch.value);
}

watch(filteredRegions, () => {

    infinitData.value = Object.fromEntries(
        Object.entries(filteredRegions.value).slice(0, countPerFetch.value)
    );
    showTrigger.value = true;
});


</script>


<style lang="scss" scoped>
.no-search {
    .title {
        font-size: 1.5em;
        font-weight: 300;
        color: var(--app-gray-color);
        text-transform: uppercase;
        letter-spacing: .1em;
    }

    .icon {
        color: var(--app-primary-color);
    }
}

.spinner {
    color: #d5d5d5;
}
</style>