<template>
    <div class="container-fluid card-foot mb-3">

        <div class="row align-items-center">
            <div class="col">
                <span class="dispo">{{ infoRef.statut }}</span>
            </div>
            <div class="col">
                <status-component :status="infoRef.statut"></status-component>
            </div>
        </div>


        <div class="form-parent d-flex flex-column justify-content-center align-items-center" v-if="isAdmin">
            <div class="me-3 h-100" v-if="showLoader">
                <div class="spinner-grow text-type-color text-center" role="status">
                    <span class="visually-hidden">Loading...</span>
                </div>
            </div>
            <div class="col-12 my-2" v-if="!showLoader">
                <form action="" @submit.prevent="">
                    <div class="form-check" v-for="(s, index ) in availableStatus" :key="s.name">
                        <input class="form-check-input" type="radio" :value="s.value" :name="s.name" :id="`${uid}_${index}`"
                            v-model="status">
                        <label class="form-check-label" :for="`${uid}_${index}`">
                            {{ s.value }}
                        </label>
                    </div>
                </form>
            </div>

            <div class="form-footer w-100 col d-flex justify-content-between align-items-center" v-if="!showLoader">

                <button class="btn btn-success btn-sm" @click="sendEdit(uid)">Save</button>


                <div class="msg text-center w-75 p-2">
                    <div class="alert m-0 p-2" :class="alertStyle">
                        {{ alertMsg }}
                    </div>
                </div>

            </div>
        </div>

    </div>
</template>



<script setup>

import { ref, watch, computed } from "vue";
import StatusComponent from "@/components/app-ui/StatusComponent";

import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from 'pinia';

const appDataStore = useAppDataStore();
const { orbitalConfig, currentUID } = storeToRefs(appDataStore);
const availableStatus = computed(() => {
    const conf = orbitalConfig.value.hotspots;
    return conf.status;
});

const regionsStore = useRegionsStore();
const { regions, isError } = storeToRefs(regionsStore);


const props = defineProps({
    cardInfo: Object,
    colorType: String,
    isAdmin: {
        type: Boolean,
        default: false
    }
});

const infoRef = ref(props.cardInfo);
const uid = computed(() => infoRef.value[currentUID.value]);

const status = ref(infoRef.value.statut);
const lastStatus = ref(status.value);

const showLoader = ref(false);
const alertMsg = ref(infoRef.value.statut);
const isReqError = ref(false);
const alertStyle = computed(() => (
    {
        'alert-success': infoRef.value.statut === "Disponible" && !isReqError.value,
        'alert-danger': infoRef.value.statut === "Indisponible" && !isReqError.value,
        'alert-warning': (infoRef.value.statut === "Rserv" && !isReqError.value) || isReqError.value,
    }
));


function sendEdit(id) {

    if (lastStatus.value === status.value) return;
    const delay = 1000;
    showLoader.value = true;
    alertMsg.value = 'Loading';
    isReqError.value = false;
    regionsStore.editStatus(id, status.value).then(msg => {
        if (isError.value) {
            showLoader.value = false;
            isReqError.value = true;
            alertMsg.value = msg;
            window.setTimeout(() => {
                const stat = infoRef.value.statut;
                isReqError.value = false;
                alertMsg.value = stat;
                lastStatus.value = alertMsg.value = status.value = stat;
            }, delay);
            return;
        }

        infoRef.value = regions.value[id];
        lastStatus.value = status.value = infoRef.value.statut;
        isReqError.value = false;
        showLoader.value = false;
        alertMsg.value = msg;

        window.setTimeout(() => {
            alertMsg.value = infoRef.value.statut;
        }, delay);
    });
}



watch(() => props.cardInfo, () => {
    infoRef.value = props.cardInfo;
    lastStatus.value = status.value = infoRef.value.statut;
    alertMsg.value = infoRef.value.statut;
});


</script>



<style lang="scss" scoped>
.text-type-color {
    color: v-bind(colorType);
}

.dispo {
    font-size: .8em;
    color: var(--app-gray-color);
    text-transform: uppercase;
}

.form-parent {
    min-height: 8em;
}

input,
label {
    cursor: pointer;
}

label {
    font-size: .8em;
    font-weight: bold;
    color: #6a6a6a;
}

.form-footer {
    height: 4em;

    .msg {
        font-size: .9em;
    }
}
</style>