<template>
    <div class="orital-loader position-fixed" v-if="canShow" ref="loaderParent">
        <div class="h-100 d-flex flex-column justify-content-center align-items-center" v-if="!isError">

            <p class="loading-txt" ref="loadingTxt">Loading {{ currentOrbital.title }}</p>
            <div class="progress" ref="progress">
                <div class="progress-bar" role="progressbar" aria-label="range-loading" :aria-valuenow="percentLoaded"
                    aria-valuemin="0" aria-valuemax="100"></div>
            </div>

            <div class="text-center">
                <p class="percentage" ref="percentage">{{ percentLoaded }}%</p>
            </div>


        </div>

    </div>
</template>


<script setup>

import { ref, watchEffect, computed } from "vue";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from "pinia";
import { gsap } from "gsap";

const appDataStore = useAppDataStore();
const { currentOrbital } = storeToRefs(appDataStore);


defineEmits([
    "onRefresh"
]);

const props = defineProps({
    title: {
        type: String,
        default: "Orbital"
    },
    show: {
        type: Boolean,
        default: true
    },
    percentLoaded: {
        type: Number,
        default: 0
    },
    isError: {
        type: Boolean,
        default: false
    }
});

const progress = ref(null);
const loadingTxt = ref(null);
const percentage = ref(null);
const loaderParent = ref(null);

const canShow = ref(true);
const animFinished = ref(false);

watchEffect(() => {
    if (!props.show && !animFinished.value) {
        _loaderAnimOut();
    }
});

const percentRangeLoading = computed(() => {
    return props.percentLoaded + "%";
});


function _loaderAnimOut() {

    gsap.to(progress.value, {
        width: 0,
        duration: 1.5,
        delay: .8,
        ease: "expo.inOut()",
    });

    gsap.to(loadingTxt.value, {
        y: 10,
        opacity: 0,
        duration: .7,
        delay: .5,
        ease: "expo.inOut()",
    });

    gsap.to(percentage.value, {
        y: -10,
        opacity: 0,
        duration: .7,
        delay: .5,
        ease: "expo.inOut()",
    });

    gsap.to(loaderParent.value, {
        x: -window.outerWidth,
        opacity: .9,
        duration: 1.5,
        delay: 1.85,
        ease: "expo.inOut()",
        onComplete: () => {
            gsap.set(loaderParent.value, { x: "-100%", opacity: 1 });
            canShow.value = false;
            animFinished.value = true;
        }
    });
}
</script>




<style lang="scss" scoped>
.orital-loader {
    z-index: 220;
    width: 100%;
    height: 100%;
    background-color: #fafafa;

    .progress {
        width: 30%;
        height: 0.1em;
        margin: 1em 0;

        .progress-bar {
            width: v-bind(percentRangeLoading);
            background-color: var(--app-primary-color);
        }
    }

    .loading-txt {
        color: var(--app-secondary-color);
        font-size: .8em;
        letter-spacing: .2em;
        text-transform: uppercase;
        margin: 1em 0;
    }

    .percentage {
        font-size: .9em;
        letter-spacing: .1em;
        color: var(--app-gray-color);
    }

}
</style>