<template>
    <div class="compass">
        <div class="compass-inner">
            <div class="main-arrow" ref="mainArrow">
                <div class="arrow-up"></div>
                <div class="arrow-down"></div>
            </div>
        </div>
    </div>
</template>


<script setup>

import { gsap } from "gsap";
import { ref } from "vue";


const mainArrow = ref(null);

function rotate(deg) {
    gsap.to(mainArrow.value, {
        rotate: deg + '_short', //_short for reversing rotation when its about 360 to 0
        duration: 0.3,
        overwrite: true
    })
}

defineExpose({
    rotate
});



</script>



<style lang="scss" scoped>
$size-compass : 4.1em;
$size-compass-inner : 5.3em;
$arrow-width : 0.2em;
$arrow-height : 1.6em;



.compass {
    position: absolute;
    right: 1rem;
    top: 0.8rem;
    z-index: 5;
    font-size: .8rem;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;

    .compass-inner {
        width: $size-compass-inner;
        height: $size-compass-inner;
        background-color: transparent;
        border-radius: 100%;
        border: .1em solid var(--app-secondary-color);
        display: flex;
        justify-content: center;
        align-items: center;


        .main-arrow {
            height: auto;
            width: auto;
            padding-top: 0;
            position: relative;
            color: var(--app-gray-color);

            &::before {
                content: 'N';
                position: absolute;
                left: 50%;
                top: -30%;
                font-size: .6em;
                transform: translateX(-50%);
            }

            &::after {
                content: 'S';
                position: absolute;
                left: 50%;
                bottom: -30%;
                font-size: .6em;
                transform: translateX(-50%);
            }

            .arrow-up {
                width: 0;
                height: 0;
                border-left: $arrow-width solid transparent;
                border-right: $arrow-width solid transparent;
                border-bottom: $arrow-height solid var(--app-secondary-color);
            }

            .arrow-down {
                width: 0;
                height: 0;
                transform: rotate(180deg);
                border-left: $arrow-width solid transparent;
                border-right: $arrow-width solid transparent;
                border-bottom: $arrow-height solid #00000025;
            }
        }
    }
}
</style>