<template>
    <div class="card-parent shadow-sm">
        <div class="card-head w-100 mb-2">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-12 color-type"></div>
                </div>
                <div class="row card-title text-center align-items-center text-white py-2 text-uppercase py-3">
                    <div class="col-12 p-0">
                        <p class="p-0 m-0"> <b>Appt-{{ titleInfo }}</b> <sup>Bloc-{{ titleInfoSmall }}</sup></p>
                    </div>
                </div>
            </div>
        </div>

        <div class="container-fluid">
            <div class="row">
                <div class="card-image d-flex align-items-center p-1">
                    <ImageComponent :src="imgUrl" />
                </div>
            </div>
        </div>

        <hr />

        <div class="container-fluid">
            <div class="card-content row align-items-center" v-for="(v, name, index) in AllInfo " :key="index">
                <div class="col-5 content-title p-2">
                    <span>{{ name }}</span>
                </div>
                <div class="col-7 content-value">
                    <span v-html="`${cardInfo[v.key]} ${v.suffixTxt}`"></span>
                </div>
            </div>
        </div>

        <hr />

        <edit-appt :card-info="cardInfo" :color-type="typeColor" :is-admin="isAdmin"></edit-appt>
    </div>
</template>


<script setup>

import { computed } from "vue";
import ImageComponent from "@/components/app-ui/ImageComponent";
import EditAppt from "@/components/admin/EditAppt";
import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const { AllInfo, titleInfoScheme } = storeToRefs(regionsStore);

const appData = useAppDataStore();
const { currentOrbital } = storeToRefs(appData);


const props = defineProps({
    cardInfo: Object,
    typesColors: Object,
    isAdmin: {
        type: Boolean,
        default: false
    }
});

const typeColor = computed(() => {
    const colorsProps = props.typesColors;
    const type = props.cardInfo.type;
    return colorsProps[type] || "#cecece";
});

const imgUrl = computed(() => {
    return `assets/images/lots/${currentOrbital.value.id}/3d/${props.cardInfo.nom}.jpg`
});

const titleInfo = computed(() => {
    const a = props.cardInfo[titleInfoScheme.value.normal];
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});
const titleInfoSmall = computed(() => {
    const a = props.cardInfo[titleInfoScheme.value.small];
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});



</script>

<style lang="scss" scoped>
$type-color : v-bind(typeColor);

.card-parent {

    border: 1px solid #e6e6e6;
    overflow: hidden;
    border-radius: .2em;

    hr {
        color: var(--app-secondary-color);
    }

    .card-head {

        letter-spacing: .1em;
        font-size: .9em;

        .color-type {
            height: .5em;
            background-color: $type-color;
        }

        .card-title {
            background-color: var(--app-gray-color);
        }
    }

    .card-image {
        overflow: hidden;
        min-height: 10em;
    }

    .card-content {
        font-size: .85em;

        .content-title {

            span {
                color: var(--app-gray-color);
            }
        }

        .content-value {
            text-align: end;

            span {
                font-weight: bold;
                color: $type-color;
                filter: brightness(0.85);
            }
        }
    }

}
</style>